/* SystemReports.module.css */

.container {
  padding: 20px;
}

.loading,
.error {
  text-align: center;
  margin: 20px;
}

.reportItem {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 6px;
  padding: 12px 16px;
  margin-bottom: 10px;
}

.label {
  font-weight: bold;
  color: #555;
}

.value {
  color: #222;
}
