.dashboard {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #f4f4f4;
  font-family: 'Arial', sans-serif;
  overflow: hidden;
}

/* Header */
.header {
  width: 100%;
  background-color: #007467;
  color: white;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  flex-shrink: 0;
}

.headerLeft {
  display: flex;
  align-items: center;
  gap: 10px;
}

.logo {
  height: 60px;
  width: 50px;
}

.headertitle {
  color: white;
  margin: 0;
  font-size: 24px;
}

.logoutBtn {
  background: none;
  border: none;
  color: white;
  font-size: 20px;
  cursor: pointer;
  padding-right: 8%;
}

.logoutBtn:hover {
  color: #ff4d4d;
}

/* Main Layout */
.mainContainer {
  display: flex;
  flex-grow: 1;
  overflow: hidden;
}

/* Sidebar */
.sidebar {
  width: 250px;
  background-color: white;
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
  transition: transform 0.3s ease;
}

.sidebar h2 {
  color: #333;
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
}

.sidebar button {
  display: block;
  padding: 10px;
  margin: 10px 0;
  font-size: 16px;
  color: #333;
  background-color: #f0f0f0;
  border: none;
  border-radius: 5px;
  text-align: left;
  width: 100%;
  cursor: pointer;
}

.sidebar button:hover {
  background-color: #e0e0e0;
}

/* Main Content */
.content {
  flex-grow: 1;
  padding: 0;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
  height: calc(100vh - 60px);
}

.iframe {
  width: 100%;
  height: 100%;
  border: none;
}

/* Toggle Button */
.toggleBtn {
  background-color: #007467;
  color: white;
  border: none;
  padding: 10px;
  font-size: 18px;
  cursor: pointer;
}

/* Responsive */
@media (max-width: 768px) {
  .sidebar {
    width: 100%;
    position: absolute;
    z-index: 1000;
    top: 60px;
    left: 0;
  }

  .mainContainer {
    flex-direction: column;
  }

  .content {
    margin-top: 20px;
  }
}
