/* src/components/user/AttendanceSection.module.css */

.container {
  padding: 40px;
  background-color: #f8f9fa;
  min-height: 100vh;
  font-family: 'Arial', sans-serif;
  box-sizing: border-box;
}

h2 {
  text-align: center;
  color: #007467;
  font-size: 26px;
  margin-bottom: 20px;
}

.message {
  margin: 0 auto 20px;
  padding: 12px;
  border-radius: 6px;
  max-width: 700px;
  text-align: center;
  font-size: 16px;
}

.error {
  background-color: #fdecea;
  color: #b0392b;
}

.info {
  background-color: #e6f7f4;
  color: #034d37;
}

.tableWrapper {
  overflow-x: auto;
  margin-bottom: 20px;
}

.table {
  width: 100%;
  max-width: 800px;
  margin: 0 auto 20px;
  border-collapse: collapse;
}

.table th,
.table td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: left;
}

.table th {
  background-color: #007467;
  color: white;
  font-weight: bold;
}

.table td {
  background-color: #fff;
  color: #333;
}

.table tbody tr:nth-child(even) td {
  background-color: #f5f5f5;
}

.refreshBtn {
  background-color: #007467;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  display: block;
  margin: 0 auto;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.refreshBtn:hover {
  background-color: #005f56;
  transform: translateY(-1px);
}

.refreshBtn:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}
