/* BackupRestore.module.css */

.container {
  padding: 20px;
  text-align: center;
}

.buttons {
  display: flex;
  gap: 16px;
  justify-content: center;
  margin-top: 16px;
}

.btn {
  background-color: #0077b6;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
}

.btn:disabled {
  background-color: #555;
  cursor: not-allowed;
}

.message {
  margin-top: 12px;
  color: #333;
  font-weight: bold;
}
