/* src/components/admin/EventsSection.module.css */

.container {
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  box-sizing: border-box;
}

h2 {
  color: #007467;
  font-size: 26px;
  margin-bottom: 16px;
  text-align: center;
}

.sectionTitle {
  margin-top: 40px;
  margin-bottom: 16px;
  font-size: 24px;
  color: #007467;
  text-align: center;
}

.message {
  margin: 0 auto 20px;
  padding: 12px;
  border-radius: 6px;
  max-width: 800px;
  text-align: center;
  font-size: 16px;
}

.error {
  background-color: #fdecea;
  color: #b0392b;
}

.success {
  background-color: #e6f7f4;
  color: #034d37;
}

.form {
  background-color: #fff;
  border-radius: 12px;
  padding: 20px;
  max-width: 800px;
  margin: 0 auto 30px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.field {
  margin-bottom: 16px;
}

.field label {
  display: block;
  margin-bottom: 6px;
  font-weight: bold;
  color: #333;
}

.field input[type='date'],
.field input[type='time'],
.field input[type='text'],
.field textarea {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 8px;
  font-size: 14px;
  box-sizing: border-box;
}

.buttonRow {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-top: 20px;
}

.submitBtn {
  background-color: #007467;
  color: white;
  border: none;
  padding: 10px 24px;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.submitBtn:hover {
  background-color: #005f56;
  transform: translateY(-1px);
}

.cancelBtn {
  background-color: #6c757d;
  color: white;
  border: none;
  padding: 10px 24px;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.cancelBtn:hover {
  background-color: #5a6268;
  transform: translateY(-1px);
}

.list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.eventCard {
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.eventHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #007467;
  color: white;
  padding: 12px 16px;
}

.actionBtns {
  display: flex;
  gap: 8px;
}

.editBtn {
  background-color: #ffc107;
  color: black;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.editBtn:hover {
  background-color: #e0a800;
}

.deleteBtn {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.deleteBtn:hover {
  background-color: #c82333;
}

.eventBody {
  padding: 16px;
}

.eventBody p {
  margin: 6px 0;
  font-size: 14px;
  color: #333;
}

.instructions {
  margin-top: 12px;
  white-space: pre-wrap; /* preserve line breaks */
}

.eventFooter {
  background-color: #f1f1f1;
  padding: 8px 16px;
  font-size: 12px;
  color: #555;
  text-align: right;
}
