/* MasterDashboard.module.css */

.dashboard {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #121212;
  font-family: 'Segoe UI', sans-serif;
  color: #e0e0e0;
}

.header {
  background: linear-gradient(to right, #0f0f0f, #1c1c1c);
  color: #00ffcc;
  padding: 1rem 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0, 255, 200, 0.1);
}

.headerLeft {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.toggleBtn {
  font-size: 1.2rem;
  background: none;
  border: none;
  color: #00ffcc;
  cursor: pointer;
}

.logo {
  height: 40px;
  filter: brightness(1.3);
}

.headerTitle {
  font-size: 1.5rem;
  font-weight: bold;
  text-shadow: 0 0 8px rgba(0, 255, 200, 0.4);
}

.logoutBtn {
  background: none;
  border: none;
  color: #00ffcc;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.logoutBtn:hover {
  transform: scale(1.1);
}

.mainContainer {
  display: flex;
  flex: 1;
  overflow: hidden;
}

.sidebar {
  width: 230px;
  background: linear-gradient(to bottom, #1a1a1a, #121212);
  padding: 1.5rem 1rem;
  color: #eee;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 0 6px rgba(0, 0, 0, 0.4);
}

.sidebarHeader {
  text-align: center;
  margin-bottom: 1.5rem;
}

.sidebarHeader h2 {
  margin: 0;
  font-size: 1rem;
  color: #aaa;
}

.sidebarHeader p {
  margin: 0.2rem 0 0;
  font-size: 1.1rem;
  font-weight: bold;
  color: #fff;
}

.sidebar button {
  background: none;
  border: none;
  padding: 0.75rem 1rem;
  color: #ccc;
  text-align: left;
  font-size: 0.95rem;
  cursor: pointer;
  transition: background 0.2s ease, padding-left 0.2s ease;
}

.sidebar button:hover,
.sidebar button:focus {
  background-color: rgba(0, 255, 200, 0.08);
  color: #00ffcc;
  padding-left: 1.5rem;
  border-left: 3px solid #00ffcc;
}

.content {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
  background: #1a1a1a;
}

.loading {
  text-align: center;
  color: #999;
  font-size: 1.2rem;
  margin-top: 2rem;
}
