/* PlatformConfig.module.css */

.container {
  padding: 20px;
}

.loading,
.error {
  text-align: center;
  margin: 20px;
}

.table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 16px;
}

.table th,
.table td {
  border: 1px solid #ccc;
  padding: 8px 12px;
  text-align: left;
}

.table th {
  background-color: #f5f5f5;
}

.input {
  width: 100%;
  padding: 6px 8px;
  font-size: 1rem;
  border: 1px solid #aaa;
  border-radius: 4px;
}

.saveBtn {
  margin-top: 16px;
  background-color: #0077b6;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.saveBtn:disabled {
  background-color: #555;
  cursor: not-allowed;
}

.message {
  margin-top: 10px;
  color: #333;
}
