.container {
  max-width:600px; margin:1rem auto; padding:1rem; background:#fff; border-radius:8px;
}
.list { list-style:none; padding:0; }
.item {
  display:flex; justify-content:space-between; align-items:center;
  padding:0.75rem; border-bottom:1px solid #eee;
}
.time { font-size:0.8rem; color:#666; }
.readBtn {
  background:#007467;color:white;border:none;padding:0.5rem 1rem;border-radius:4px;
  cursor:pointer;transition:background 0.2s;
}
.readBtn:hover { background:#005f56; }
.empty { text-align:center; color:#666; padding:1rem; }
