/* src/components/user/ContactUsSection.module.css */

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
  background-color: #f8f9fa;
  font-family: 'Arial', sans-serif;
  box-sizing: border-box;
}

h2 {
  color: #007467;
  font-size: 26px;
  text-align: center;
  margin-bottom: 20px;
}

.form {
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 30px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.form label {
  display: block;
  margin-bottom: 6px;
  font-weight: bold;
  color: #333;
}

.form textarea {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 10px;
  font-size: 14px;
  resize: vertical;
  box-sizing: border-box;
  margin-bottom: 12px;
}

.form button {
  background-color: #007467;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.form button:hover {
  background-color: #005f56;
}

.feedback {
  max-width: 800px;
  margin: 0 auto 20px;
  padding: 12px;
  border-radius: 6px;
  text-align: center;
  font-size: 16px;
}

.error {
  background-color: #fdecea;
  color: #b0392b;
}

.success {
  background-color: #e6f7f4;
  color: #034d37;
}

.loadingText {
  font-style: italic;
  color: #555;
  text-align: center;
}

.noData {
  text-align: center;
  font-style: italic;
  color: #666;
}

.listWrapper {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.queryCard {
  background-color: #fff;
  border-radius: 10px;
  padding: 16px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1);
}

.queryHeader {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  font-size: 14px;
  color: #555;
}

.queryDate {
  font-style: italic;
}

.status {
  font-weight: bold;
}

.userMessage,
.adminResponse,
.pending {
  margin: 8px 0;
  font-size: 15px;
  color: #333;
}

.adminResponse {
  background-color: #e6f7f4;
  padding: 8px;
  border-radius: 6px;
}

.pending {
  color: #a07d00;
  font-style: italic;
}
