/* src/components/admin/SupportQueriesSection.module.css */

.container {
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  box-sizing: border-box;
}

h2 {
  color: #007467;
  font-size: 28px;
  text-align: center;
  margin-bottom: 20px;
}

.feedback {
  max-width: 900px;
  margin: 0 auto 20px;
  padding: 12px;
  border-radius: 6px;
  text-align: center;
  font-size: 16px;
}

.error {
  background-color: #fdecea;
  color: #b0392b;
}

.success {
  background-color: #e6f7f4;
  color: #034d37;
}

.loadingText {
  font-style: italic;
  color: #555;
  text-align: center;
}

.noData {
  text-align: center;
  font-style: italic;
  color: #666;
  margin-top: 20px;
}

.listWrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.queryCard {
  background-color: #fff;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  position: relative;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
  font-size: 14px;
  color: #333;
}

.dates span {
  margin-left: 16px;
  font-style: italic;
  color: #555;
}

.userMessage {
  margin: 12px 0;
  font-size: 15px;
  color: #333;
}

.responded {
  background-color: #e6f7f4;
  padding: 10px;
  border-radius: 6px;
  margin: 12px 0;
  font-size: 15px;
  color: #333;
}

.replyBtn,
.deleteBtn {
  background-color: #007467;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  margin-right: 8px;
  transition: background-color 0.3s ease;
}

.replyBtn:hover,
.deleteBtn:hover {
  background-color: #005f56;
}

.deleteBtn {
  background-color: #dc3545;
}

.deleteBtn:hover {
  background-color: #c82333;
}

.replySection {
  margin-top: 12px;
}

.replySection textarea {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 8px;
  font-size: 14px;
  box-sizing: border-box;
  resize: vertical;
}

.replyActions {
  margin-top: 8px;
  display: flex;
  gap: 8px;
}

.submitReplyBtn {
  background-color: #007467;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submitReplyBtn:hover {
  background-color: #005f56;
}

.cancelBtn {
  background-color: #6c757d;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
}

.cancelBtn:hover {
  background-color: #5a6268;
}
