/* GlobalSearch.module.css */

.container {
  padding: 20px;
}

.form {
  display: flex;
  gap: 8px;
  margin-bottom: 16px;
}

.input {
  flex: 1;
  padding: 8px 12px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.button {
  padding: 8px 16px;
  background-color: #0077b6;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.button:disabled {
  background-color: #555;
  cursor: not-allowed;
}

.error {
  color: #c0392b;
  margin-bottom: 12px;
}

.section {
  margin-top: 24px;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

.table th,
.table td {
  border: 1px solid #ddd;
  padding: 8px 12px;
  text-align: left;
}

.table th {
  background-color: #f0f0f0;
}
