/* src/components/admin/ViewAttendance.module.css */

.container {
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  box-sizing: border-box;
}

h2 {
  color: #007467;
  font-size: 26px;
  text-align: center;
  margin-bottom: 20px;
}

.message {
  margin: 0 auto 20px;
  padding: 12px;
  border-radius: 6px;
  max-width: 800px;
  text-align: center;
  font-size: 16px;
}

.error {
  background-color: #fdecea;
  color: #b0392b;
}

.info {
  background-color: #e6f7f4;
  color: #034d37;
}

.controls {
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: center;
  margin-bottom: 20px;
}

.controls label {
  font-weight: bold;
  color: #333;
}

.select {
  padding: 6px 8px;
  font-size: 14px;
  border-radius: 6px;
  border: 1px solid #ccc;
  min-width: 280px;
}

.loadingText {
  font-style: italic;
  color: #555;
}

.tableWrapper {
  overflow-x: auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.table th,
.table td {
  padding: 12px 8px;
  border: 1px solid #ddd;
  text-align: left;
  font-size: 14px;
}

.table th {
  background-color: #007467;
  color: white;
  font-weight: normal;
}

.table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.noData {
  text-align: center;
  font-style: italic;
  color: #666;
  margin-top: 20px;
}
