/* src/components/admin/ManageAchievementsSection.module.css */

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  box-sizing: border-box;
}

h2 {
  color: #007467;
  font-size: 26px;
  margin-bottom: 16px;
  text-align: center;
}

.sectionTitle {
  margin-top: 40px;
  margin-bottom: 16px;
  color: #007467;
  font-size: 24px;
  text-align: center;
}

.message {
  margin: 0 auto 20px;
  padding: 12px;
  border-radius: 6px;
  max-width: 800px;
  text-align: center;
  font-size: 16px;
}

.error {
  background-color: #fdecea;
  color: #b0392b;
}

.success {
  background-color: #e6f7f4;
  color: #034d37;
}

.form {
  background-color: #fff;
  border-radius: 12px;
  padding: 20px;
  max-width: 800px;
  margin: 0 auto 30px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.field {
  margin-bottom: 20px;
}

.field label {
  display: block;
  margin-bottom: 6px;
  font-weight: bold;
  color: #333;
}

.field input[type="text"],
.field textarea {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 8px;
  font-size: 14px;
}

.field input[type="file"] {
  font-size: 14px;
}

.submitBtn {
  background-color: #007467;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  display: block;
  margin: 0 auto;
}

.submitBtn:hover {
  background-color: #005f56;
  transform: translateY(-1px);
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.card {
  background-color: #fff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}

.cardImage {
  width: 100%;
  object-fit: cover;
  height: 180px;
}

.cardBody {
  padding: 16px;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.cardTitle {
  margin: 0 0 8px;
  font-size: 18px;
  color: #007467;
}

.cardDesc {
  flex: 1;
  margin: 0 0 12px;
  font-size: 14px;
  color: #555;
}

.cardDate {
  font-size: 12px;
  color: #888;
  margin-bottom: 12px;
}

.deleteBtn {
  background-color: #dc3545;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  align-self: flex-start;
}

.deleteBtn:hover {
  background-color: #c82333;
}
