.container {
  max-width: 900px;
  margin: 40px auto;
  padding: 30px;
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  font-family: 'Roboto', sans-serif;
}

h2 {
  color: #007467;
  margin-bottom: 20px;
  font-size: 28px;
  text-align: center;
}

.section {
  margin-bottom: 30px;
}

.section h3 {
  color: #007467;
  margin-bottom: 15px;
  font-size: 22px;
  border-bottom: 2px solid #007467;
  padding-bottom: 10px;
}

input,
select {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

input:focus,
select:focus {
  border-color: #007467;
  outline: none;
}

input[readonly] {
  background-color: #f9f9f9;
  cursor: not-allowed;
}

.submitBtn {
  background-color: #007467;
  color: white;
  padding: 12px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  width: 100%;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

.submitBtn:hover {
  background-color: #005f56;
}

.pictureSection {
  text-align: center;
  margin-bottom: 20px;
}

.pictureSection img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #007467;
  margin-bottom: 10px;
}

.pictureSection input[type="file"] {
  display: block;
  margin: 0 auto;
}
