/* src/components/admin/AdminReportsSection.module.css */

.container {
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  box-sizing: border-box;
}

h2 {
  color: #007467;
  font-size: 30px;
  text-align: center;
  margin-bottom: 24px;
}

.message {
  margin: 0 auto 20px;
  padding: 12px;
  border-radius: 6px;
  max-width: 800px;
  text-align: center;
  font-size: 16px;
}

.error {
  background-color: #fdecea;
  color: #b0392b;
}

.info {
  background-color: #e6f7f4;
  color: #034d37;
}

/* Summary Cards */
.cardsWrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-between;
  margin-bottom: 40px;
}

.card {
  background-color: #fff;
  flex: 1 1 45%;
  min-width: 240px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  text-align: center;
}

.card h3 {
  margin: 0 0 10px;
  font-size: 18px;
  color: #333;
}

.cardValue {
  font-size: 28px;
  color: #007467;
  margin: 0;
}

.loadingText {
  font-style: italic;
  color: #555;
}

/* Section Title */
.sectionTitle {
  font-size: 22px;
  color: #007467;
  margin-bottom: 16px;
  text-align: center;
}

/* Attendance Table */
.tableWrapper {
  overflow-x: auto;
  margin-bottom: 40px;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

.table th,
.table td {
  padding: 12px 8px;
  border: 1px solid #ddd;
  text-align: center;
  font-size: 14px;
}

.table th {
  background-color: #007467;
  color: white;
  font-weight: normal;
}

.table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.noData {
  text-align: center;
  font-style: italic;
  color: #666;
  margin-top: 20px;
}
