.profileContainer {
  max-width: 500px;
  margin: 40px auto;
  padding: 30px;
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

.profileHeader img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #007467;
  margin-bottom: 20px;
}

.profileHeader h2 {
  font-size: 24px;
  color: #333;
}

.profileDetails {
  margin-top: 20px;
  text-align: left;
}

.profileDetails p {
  margin: 15px 0;
  font-size: 16px;
  color: #555;
}

.profileDetails strong {
  color: #007467;
}

.profileBtn {
  display: block;
  width: 100%;
  padding: 12px;
  margin: 15px 0;
  background-color: #007467;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.profileBtn:hover {
  background-color: #005f56;
}

.profileBtn i {
  margin-right: 8px;
}
