/* NotificationManager.module.css */

.container {
  padding: 20px;
}

.loading,
.error {
  text-align: center;
  margin: 20px;
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.item {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 12px 16px;
  margin-bottom: 12px;
}

.header {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 6px;
}

.type {
  font-weight: bold;
  color: #0077b6;
}

.cadet {
  font-size: 0.9rem;
  color: #555;
}

.timestamp {
  margin-left: auto;
  font-size: 0.8rem;
  color: #999;
}

.message {
  margin: 4px 0 8px;
  color: #333;
}

.link {
  color: #0077b6;
  text-decoration: none;
  font-size: 0.9rem;
}

.link:hover {
  text-decoration: underline;
}

.status {
  margin-top: 8px;
  font-size: 0.8rem;
}

.read {
  color: #28a745;
}

.unread {
  color: #c0392b;
}
