/* src/components/user/EventsSection.module.css */

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  box-sizing: border-box;
}

h2 {
  text-align: center;
  color: #007467;
  font-size: 26px;
  margin-bottom: 16px;
}

.message {
  margin: 0 auto 20px;
  padding: 12px;
  border-radius: 6px;
  max-width: 700px;
  text-align: center;
  font-size: 16px;
}

.error {
  background-color: #fdecea;
  color: #b0392b;
}

.info {
  background-color: #e6f7f4;
  color: #034d37;
}

.list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.eventCard {
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.header {
  background-color: #007467;
  color: white;
  padding: 12px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dressCode {
  font-size: 14px;
  font-style: italic;
}

.body {
  padding: 16px;
}

.body p {
  margin: 6px 0;
  font-size: 14px;
  color: #333;
}

.instructions {
  margin-top: 12px;
  white-space: pre-wrap; /* preserve newlines */
}

.footer {
  background-color: #f1f1f1;
  padding: 8px 16px;
  font-size: 12px;
  color: #555;
  text-align: right;
}

.refreshBtn {
  background-color: #007467;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  display: block;
  margin: 20px auto 0;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.refreshBtn:hover {
  background-color: #005f56;
  transform: translateY(-1px);
}

.refreshBtn:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}
