/* src/components/user/FallinDetailsSection.module.css */

.container {
  padding: 1.5rem;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 6px;
  margin-bottom: 2rem;
}

h2 {
  margin-top: 0;
}

.message {
  background-color: #fdecea;
  color: #b0392b;
  padding: 0.5rem;
  border-radius: 4px;
  margin-bottom: 1rem;
  text-align: center;
}

.tableWrapper {
  overflow-x: auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table th,
.table td {
  border: 1px solid #ccc;
  padding: 0.5rem;
  text-align: left;
}
.table th {
  background-color: #f5f5f5;
}

.noData {
  text-align: center;
  color: #555;
  padding: 1rem;
}

.refreshBtn {
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background-color: #007bff;
  border: none;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
}
