/* SystemLogs.module.css */

.container {
  padding: 20px;
}

.loading,
.error {
  text-align: center;
  margin: 20px;
}

.table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 16px;
}

.table th,
.table td {
  border: 1px solid #ccc;
  padding: 8px 12px;
  text-align: left;
}

.table th {
  background-color: #f5f5f5;
}
