/* SupportQueries.module.css */

.container {
  padding: 20px;
}

.loading,
.error {
  text-align: center;
  margin: 20px;
}

.table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 16px;
}

.table th,
.table td {
  border: 1px solid #ccc;
  padding: 8px 12px;
  text-align: left;
}

.table th {
  background-color: #f5f5f5;
}

.deleteBtn {
  background-color: #c0392b;
  color: white;
  border: none;
  padding: 6px 10px;
  border-radius: 4px;
  cursor: pointer;
}

.deleteBtn:hover {
  background-color: #a93226;
}
