/* ProfileSection.module.css */

.container {
  display: flex;
  min-height: 100vh;
  background-color: #f8f9fa;
}

.main {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.header {
  color: #00897b;
  padding: 1rem;
  text-align: center;
  font-size: 1.75rem;
  font-weight: 600;
}

.content {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
}

.card {
  max-width: 64rem;
  margin: 0 auto;
  background-color: #ffffff;
  border-radius: 0.75rem;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.pictureContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.profilePic {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
  border: 4px solid #00897b;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.profilePic:hover {
  transform: scale(1.05);
}

.uploadLabel {
  padding: 0.5rem 1rem;
  background-color: #00897b;
  color: #ffffff;
  border: none;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.uploadButton:hover {
  background-color: #006d63;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
}

@media (min-width: 768px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.formGroup {
  display: flex;
  flex-direction: column;
}

.label {
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.25rem;
}

.input,
.textarea,
.select {
  padding: 0.5rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 1rem;
}

.input:focus,
.textarea:focus,
.select:focus {
  border-color: #00897b;
  outline: none;
}

.input[readonly] {
  background-color: #f3f4f6;
  cursor: not-allowed;
}

.textarea {
  resize: none;
}

.section {
  border-top: 1px solid #e5e7eb;
  padding-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.sectionTitle {
  color: #006d63;
  font-size: 1.25rem;
  font-weight: 600;
}

.submitButton {
  width: 100%;
  padding: 0.75rem;
  background-color: #00897b;
  color: #ffffff;
  border: none;
  border-radius: 0.5rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submitButton:hover {
  background-color: #006d63;
}
