/* Layout Base */
.dashboard {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'Segoe UI', sans-serif;
  background-color: #f5f5f5;
}

/* Header */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #007467;
  color: white;
  padding: 10px 20px;
}

.headerLeft {
  display: flex;
  align-items: center;
  gap: 10px;
}

.logo {
  height: 50px;
  width: 45px;
  object-fit: contain;
}

.headerTitle {
  font-size: 20px;
  color: white;
}

.logoutBtn {
  font-size: 20px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

/* Main Layout */
.mainContainer {
  display: flex;
  flex: 1;
}

/* Sidebar */
.sidebar {
  width: 230px;
  background: white;
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
}

.sidebar h2 {
  font-size: 16px;
  margin-bottom: 20px;
}

.sidebar button {
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  border: none;
  background-color: #f0f0f0;
  font-size: 15px;
  cursor: pointer;
  border-radius: 5px;
}

.sidebar button:hover {
  background-color: #e0e0e0;
}

/* Main Content */
.content {
  flex-grow: 1;
  padding: 20px;
}

/* Profile Section */
.profileCard {
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  max-width: 400px;
  margin: auto;
  text-align: center;
}

.profileImg {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 15px;
  object-fit: cover;
}

/* Section Placeholder */
.sectionCard {
  background: white;
  padding: 25px;
  border-radius: 10px;
  max-width: 600px;
  margin: auto;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

/* Loading */
.loading {
  text-align: center;
  margin-top: 50px;
}

/* Toggle */
.toggleBtn {
  background-color: #007467;
  color: white;
  font-size: 22px;
  border: none;
  cursor: pointer;
}

/* Mobile Responsive */
@media (max-width: 768px) {
  .mainContainer {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
    position: absolute;
    z-index: 100;
    top: 60px;
    left: 0;
    background-color: white;
  }

  .content {
    margin-top: 20px;
  }

  .headerTitle {
    color: white;
    font-size: 16px;
  }
}
