.profileContainer {
  padding: 2rem;
  background: #111;
  color: #fff;
  min-height: 100vh;
  font-family: 'Segoe UI', sans-serif;
}

.profileHeader {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
}

.profilePhoto {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 3px solid #0ff;
  box-shadow: 0 0 12px #0ff;
  background-color: #222;
  object-fit: cover;
}

.fullName {
  font-size: 2rem;
  font-weight: bold;
  margin-top: 1rem;
  color: #0ff;
  text-shadow: 0 0 10px #0ff;
}

.profileDetails {
  text-align: center;
  margin-bottom: 2rem;
  color: #ccc;
  font-size: 1rem;
}

.profileDetails div {
  margin: 0.5rem 0;
}

.quickAccessCards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid #222;
  padding: 1.5rem;
  border-radius: 10px;
  text-align: center;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  cursor: pointer;
  box-shadow: 0 0 15px rgba(0, 255, 255, 0.1);
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px #0ff;
}

.cardIcon {
  font-size: 2rem;
  color: #0ff;
  margin-bottom: 1rem;
}

.card h3 {
  margin: 0.5rem 0;
  color: #fff;
}

.card p {
  font-size: 0.9rem;
  color: #aaa;
}

.notificationPanel {
  margin-top: 2rem;
}

.notifications {
  background-color: #1a1a1a;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #0ff;
}

.notificationItem {
  padding: 0.5rem;
  border-bottom: 1px solid #333;
  color: #eee;
}

.noNotifications {
  color: #666;
  text-align: center;
}

.searchBar {
  margin-top: 2rem;
  text-align: center;
}

.searchBar input {
  padding: 0.5rem 1rem;
  border-radius: 20px;
  border: 1px solid #0ff;
  background: #000;
  color: #0ff;
  outline: none;
  width: 300px;
}
