.container {
  max-width: 600px;
  margin: auto;
  background-color: #121212;
  padding: 2rem;
  border-radius: 10px;
  color: #0ff;
  font-family: 'Segoe UI', sans-serif;
}

.profileCard {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1.5rem;
}

.profilePic {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 2px solid #0ff;
  object-fit: cover;
  cursor: pointer;
  margin-bottom: 1rem;
}

.uploadBtn {
  background-color: transparent;
  color: #0ff;
  border: 1px solid #0ff;
  padding: 0.4rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  margin-bottom: 1rem;
}

.uploadBtn:hover {
  background-color: #0ff;
  color: #000;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form input,
.form textarea {
  background-color: #222;
  color: #0ff;
  border: 1px solid #333;
  padding: 0.5rem;
  border-radius: 5px;
  width: 100%;
}

.submitBtn {
  background-color: #0ff;
  color: #000;
  padding: 0.6rem 1rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.submitBtn:hover {
  background-color: #00f0f0;
}

.hiddenInput {
  display: none;
}
