.container {
  padding: 20px;
  background-color: #ffffff;
  border-radius: 8px;
}

.form {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.05);
  margin-bottom: 40px;
}

.formGroup {
  margin-bottom: 16px;
}

.formGroup label {
  display: block;
  margin-bottom: 6px;
  font-weight: 600;
  color: #00695c;
}

.formGroup input[type='text'],
.formGroup input[type='date'],
.formGroup input[type='time'],
.formGroup select,
.formGroup textarea {
  width: 100%;
  padding: 10px 12px;
  border-radius: 4px;
  border: 1px solid #ccc;
  font-size: 14px;
  background-color: #fff;
  color: #000;
}

.buttonGroup {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 16px;
}

.submitBtn {
  background-color: #00695c;
  color: #fff;
  font-weight: bold;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.clearBtn {
  background-color: #cccccc;
  color: #000;
  font-weight: bold;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.submitBtn:hover {
  background-color: #004d40;
}

.clearBtn:hover {
  background-color: #999999;
}

.table {
  width: 100%;
  border-collapse: collapse;
  background-color: #ffffff;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.05);
}

.table th,
.table td {
  border: 1px solid #ddd;
  padding: 10px 12px;
  text-align: left;
  font-size: 14px;
}

.table th {
  background-color: #00695c;
  font-weight: 600;
  color: #ffffff;
}

.editBtn,
.deleteBtn {
  padding: 6px 12px;
  margin-right: 6px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 13px;
}

.editBtn {
  background-color: #00695c;
  color: #fff;
}

.deleteBtn {
  background-color: #00695c;
  color: #fff;
}

.editBtn:hover,
.deleteBtn:hover {
  background-color: #004d40;
}
/* src/components/admin/FallinDetailsSection.module.css */

.container {
  padding: 1.5rem;
  background: #ffffff;
  border: 1px solid #ddd;
  border-radius: 6px;
  margin-bottom: 2rem;
}

h2 {
  margin-top: 0;
}

.message {
  background-color: #fdecea;
  color: #b0392b;
  padding: 0.75rem;
  border-radius: 4px;
  margin-bottom: 1rem;
  text-align: center;
}

.form {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}

.formGroup {
  display: flex;
  flex-direction: column;
}

.formGroup label {
  margin-bottom: 0.25rem;
  font-weight: bold;
}

.formGroup input,
.formGroup select,
.formGroup textarea {
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.buttonGroup {
  grid-column: 1/-1;
  display: flex;
  gap: 1rem;
}

.submitBtn {
  padding: 0.6rem 1.2rem;
  background-color: #007bff;
  border: none;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
}
.clearBtn {
  padding: 0.6rem 1.2rem;
  background-color: #6c757d;
  border: none;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table th,
.table td {
  border: 1px solid #ddd;
  padding: 0.5rem;
  text-align: left;
}
.table th {
  background-color: #f5f5f5;
}
.actionBtns button {
  margin-right: 0.5rem;
  padding: 0.3rem 0.6rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.editBtn {
  background-color: #28a745;
  color: #fff;
}
.deleteBtn {
  background-color: #dc3545;
  color: #fff;
}

.noData {
  text-align: center;
  color: #555;
  padding: 1rem;
}
