/* src/components/user/NotificationsSection.module.css */

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  box-sizing: border-box;
}

h2 {
  color: #007467;
  font-size: 26px;
  text-align: center;
  margin-bottom: 20px;
}

.loadingText {
  text-align: center;
  font-style: italic;
  color: #555;
}

.errorText {
  text-align: center;
  color: #b0392b;
}

.noData {
  text-align: center;
  font-style: italic;
  color: #666;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.notificationItem {
  background-color: #fff;
  border-radius: 8px;
  margin-bottom: 16px;
  padding: 16px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.notificationItem:hover {
  background-color: #f0f0f0;
}

.unread {
  border-left: 4px solid #007467; /* marker for unread */
}

.read {
  border-left: 4px solid transparent;
  opacity: 0.7;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  font-size: 14px;
  color: #555;
}

.type {
  font-weight: bold;
  text-transform: capitalize;
}

.timestamp {
  font-style: italic;
}

.message {
  font-size: 15px;
  color: #333;
  margin: 0;
}
