/* TakeAttendance.module.css */

.container {
  padding: 40px;
  background-color: #f8f9fa;
  min-height: 100vh;
  box-sizing: border-box;
  font-family: 'Arial', sans-serif;
}

.heading {
  font-size: 28px;
  color: #007467;
  text-align: center;
  margin-bottom: 30px;
}

.message {
  margin: 0 auto 20px;
  padding: 12px;
  border-radius: 6px;
  width: 100%;
  max-width: 800px;
  text-align: center;
  font-size: 16px;
}

.error {
  background-color: #fdecea;
  color: #b0392b;
}

.success {
  background-color: #e6f7f4;
  color: #034d37;
}

.fallinList {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.fallinCard {
  background-color: #007467;
  color: white;
  border-radius: 12px;
  padding: 20px;
  width: 260px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  text-align: center;
}

.fallinCard:hover {
  background-color: #005f56;
  transform: translateY(-2px);
}

.fallinCard h3 {
  margin: 0 0 10px;
  font-size: 18px;
}

.fallinCard p {
  margin: 4px 0;
  font-size: 14px;
}

.backButton {
  background-color: #6c757d;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-bottom: 20px;
}

.backButton:hover {
  background-color: #5a6268;
}

.selectedFallinInfo {
  text-align: center;
  margin-bottom: 20px;
}

.tableWrapper {
  overflow-x: auto;
  margin-top: 10px;
}

.table {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  border-collapse: collapse;
}

.table th,
.table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.table th {
  background-color: #007467;
  color: white;
  font-weight: bold;
}

.statusSelect {
  width: 100%;
  padding: 6px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.actionBtns {
  display: flex;
  gap: 10px;
}

.editBtn {
  background-color: #28a745;
  color: #fff;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.editBtn:hover {
  background-color: #218838;
}

.deleteBtn {
  background-color: #dc3545;
  color: #fff;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.deleteBtn:hover {
  background-color: #c82333;
}

.noData {
  text-align: center;
  color: #555;
  padding: 12px;
}

/* Add at the bottom of TakeAttendance.module.css */
/* ...existing CSS... */

.submitAllBtn {
  background-color: #007467;
  color: white;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.submitAllBtn:hover {
  background-color: #005f56;
  transform: translateY(-2px);
}


.submitAllBtn:disabled {
  background-color: #999;
  cursor: not-allowed;
  transform: none;
}
