/* src/components/user/AchievementsSection.module.css */

.container {
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 20px;
  font-family: 'Arial', sans-serif;
  background-color: #f8f9fa;
  box-sizing: border-box;
}

h2 {
  text-align: center;
  color: #007467;
  font-size: 26px;
  margin-bottom: 16px;
}

.message {
  margin: 0 auto 20px;
  padding: 12px;
  border-radius: 6px;
  max-width: 700px;
  text-align: center;
  font-size: 16px;
}

.error {
  background-color: #fdecea;
  color: #b0392b;
}

.info {
  background-color: #e6f7f4;
  color: #034d37;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.card {
  background-color: #fff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}

.cardImage {
  width: 100%;
  object-fit: cover;
  height: 180px;
}

.cardBody {
  padding: 16px;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.cardTitle {
  margin: 0 0 8px;
  font-size: 18px;
  color: #007467;
}

.cardDesc {
  flex: 1;
  margin: 0 0 12px;
  font-size: 14px;
  color: #555;
}

.cardDate {
  font-size: 12px;
  color: #888;
  margin-top: auto;
}

.refreshBtn {
  background-color: #007467;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  display: block;
  margin: 0 auto;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.refreshBtn:hover {
  background-color: #005f56;
  transform: translateY(-1px);
}

.refreshBtn:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}
