/* src/components/admin/ManageUsersSection.module.css */

.container {
  padding: 40px;
  background-color: #f8f9fa;
  min-height: 100vh;
  font-family: 'Arial', sans-serif;
  box-sizing: border-box;
}

h2 {
  text-align: center;
  color: #007467;
  font-size: 28px;
  margin-bottom: 20px;
}

.section {
  margin-bottom: 40px;
}

.section h3 {
  color: #007467;
  font-size: 22px;
  margin-bottom: 12px;
}

.tableWrapper {
  overflow-x: auto;
}

.table {
  width: 100%;
  max-width: 900px;
  margin: 0 auto 20px;
  border-collapse: collapse;
}

.table th,
.table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.table th {
  background-color: #007467;
  color: white;
  font-weight: bold;
}

.table td {
  background-color: #fff;
  color: #333;
}

.table tbody tr:nth-child(even) td {
  background-color: #f5f5f5;
}

.actions {
  display: flex;
  gap: 10px;
}

.approveBtn {
  background-color: #28a745;
  color: #fff;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.approveBtn:hover {
  background-color: #218838;
}

.deleteBtn {
  background-color: #dc3545;
  color: #fff;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.deleteBtn:hover {
  background-color: #c82333;
}

.message {
  margin: 0 auto 20px;
  padding: 12px;
  border-radius: 6px;
  max-width: 900px;
  text-align: center;
  font-size: 16px;
}

.error {
  background-color: #fdecea;
  color: #b0392b;
}

.success {
  background-color: #e6f7f4;
  color: #034d37;
}
